import { Component } from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import { Heading } from '@Green-Dot-Corporation/eureka-ui-typography';
import './styles/header.scss';

export default class Header extends Component {
    static propTypes = {
        headerTitle: PropTypes.string.isRequired,
        contextCls: PropTypes.string,
    };

    render() {
        const { headerTitle, contextCls } = this.props;

        return (
            <div className={cx(this.baseCls, contextCls)}>
                <Heading className={cx(this.baseCls, contextCls)}>
                    {headerTitle}
                </Heading>
            </div>
        );
    }

    baseCls = 'dd-switch-header';
}
