import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import { Banner } from '@Green-Dot-Corporation/eureka-ui-callouts';
import { Button, VARIANTS } from '@Green-Dot-Corporation/eureka-ui-buttons';
import { EurekaCircleInfo } from '@Green-Dot-Corporation/eureka-ui-icons';
import { getTranslation } from '@Green-Dot-Corporation/eureka-lib-i18n-utils';
import DDSwitchContainer from '../dd-switch-container/DDSwitchContainer';
import Header from '../header/Header';
import './styles/dd-form.scss';
import Img from '../img/Img';

const { PRIMARY } = VARIANTS;

class DDForm extends PureComponent {
    static propTypes = {
        contextCls: PropTypes.string,
    };

    state = {};

    render() {
        const { contextCls } = this.props;

        return (
            <DDSwitchContainer contextCls={cx(this.baseCls, contextCls)}>
                <Header headerTitle="Direct deposit form" />
                {this.renderBanner()}
                {this.renderEmptyFormImg()}
                {this.renderActionButtons()}
            </DDSwitchContainer>
        );
    }

    renderBanner = () => {
        return (
            <Banner
                contextCls={`${this.baseCls}__banner-info`}
                variant="INFO"
                msg="Download the form to your device to get started. After you've completed this form,give it to your employer or government benefits provider to set up your direct deposit."
                renderIcon={this.renderIconCircleInfo}
            />
        );
    };

    renderIconCircleInfo = () => {
        return <EurekaCircleInfo contextCls={`${this.baseCls}__icon-info`} />;
    };

    renderEmptyFormImg = () => {
        return (
            <div className={`${this.baseCls}__form-container`}>
                <Img
                    filename="dd_form_empty.PNG"
                    alt={getTranslation('ddform.emptyForm.imgAlt')}
                />
            </div>
        );
    };

    renderDownloadBtn = () => {
        return (
            <Button
                contextCls={cx(`${this.baseCls}__button-download`)}
                text={getTranslation('ddform.downloadBtn.text')}
                variant={PRIMARY}
                onClick={this.handleDownloadBtnClick}
                aria-label={getTranslation('ddform.downloadBtn.text')}
            />
        );
    };

    renderActionButtons = () => {
        return (
            <div className={`${this.baseCls}__actions-container`}>
                {this.renderDownloadBtn()}
            </div>
        );
    };

    baseCls = 'dd-switch-dd-form';
}

export default DDForm;
