import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import {
    Heading,
    Hyperlink,
} from '@Green-Dot-Corporation/eureka-ui-typography';
import { Button, VARIANTS } from '@Green-Dot-Corporation/eureka-ui-buttons';
import { Callout } from '@Green-Dot-Corporation/eureka-ui-callouts';
import {
    getTranslation,
    reactI18next,
} from '@Green-Dot-Corporation/eureka-lib-i18n-utils';
import DDSwitchContainer from '../dd-switch-container/DDSwitchContainer';
import Legal from '../legal/Legal';
import './styles/bump.scss';

import Img from '../img/Img';

const { PRIMARY } = VARIANTS;

class Bump extends PureComponent {
    static propTypes = {
        contextCls: PropTypes.string,
        navigateWithinParent: PropTypes.func,
    };

    state = {};

    render() {
        const { contextCls, ...rest } = this.props;

        return (
            <DDSwitchContainer
                {...rest}
                contextCls={`${this.baseCls}__container`}
            >
                <Callout>
                    {this.renderImgs()}

                    <div className={`${this.baseCls}__text-container`}>
                        {this.renderTitle()}
                        {this.renderSubTitle()}
                        {this.renderBumpContent()}
                    </div>

                    <div className={`${this.baseCls}__actions-container`}>
                        {this.renderContinueBtn()}
                    </div>

                    {this.renderNote()}
                </Callout>
                <Legal />
            </DDSwitchContainer>
        );
    }

    renderImgs = () => {
        return (
            <div className={`${this.baseCls}__imgs-container`}>
                <span className={`${this.baseCls}__img-brand`}>
                    <Img
                        filename="mini-logo-brand.svg"
                        alt={getTranslation('ddswitch.bump.imgAlt.brandLogo')}
                    />
                </span>
                <span className={`${this.baseCls}__img-atomic`}>
                    <Img
                        filename="mini-logo-atomic.svg"
                        alt={getTranslation('ddswitch.bump.imgAlt.atomic')}
                    />
                </span>
            </div>
        );
    };

    renderTitle = () => {
        const headingLvl = 5;
        const headingText = getTranslation('ddswitch.bump.title');

        return (
            <Heading
                contextCls={`${this.baseCls}__title`}
                headingLvl={headingLvl}
            >
                {headingText}
            </Heading>
        );
    };

    renderSubTitle = () => {
        const text = getTranslation('ddswitch.bump.subtitle');

        return <div className={`${this.baseCls}__subtitle`}>{text}</div>;
    };

    renderBumpContent = () => {
        return (
            <div className={`${this.baseCls}__bump_content`}>
                <p className={`${this.baseCls}__bump_content-1`}>
                    <Img
                        filename="number-1.svg"
                        alt={getTranslation('ddswitch.bump.imgAlt.number1')}
                    />
                    <span>{getTranslation('ddswitch.bump.content.one')}</span>
                </p>
                <p className={`${this.baseCls}__bump_content-2`}>
                    <Img
                        filename="number-2.svg"
                        alt={getTranslation('ddswitch.bump.imgAlt.number2')}
                    />
                    <span>{getTranslation('ddswitch.bump.content.two')}</span>
                </p>
                <p className={`${this.baseCls}__bump_content-3`}>
                    <Img
                        filename="number-3.svg"
                        alt={getTranslation('ddswitch.bump.imgAlt.number3')}
                    />
                    <span>{getTranslation('ddswitch.bump.content.three')}</span>
                </p>
            </div>
        );
    };

    renderContinueBtn = () => {
        return (
            <Button
                contextCls={cx(`${this.baseCls}__button-continue`)}
                text={getTranslation('ddswitch.bump.btn.text')}
                variant={PRIMARY}
                onClick={this.handleContinueBtnClick}
                aria-label={getTranslation('ddswitch.bump.btn.text')}
            />
        );
    };

    renderNote = () => {
        const { Trans } = reactI18next;

        return (
            <div className={`${this.baseCls}__note`}>
                <Trans
                    i18nKey="ddswitch.bump.note"
                    components={{
                        termsLink: (
                            <Hyperlink
                                shouldOpenInNewWindow
                                href={getTranslation(
                                    'ddswitch.bump.note.termsLink',
                                )}
                            />
                        ),
                        ppLink: (
                            <Hyperlink
                                shouldOpenInNewWindow
                                href={getTranslation(
                                    'ddswitch.bump.note.ppLink',
                                )}
                            />
                        ),
                    }}
                />
            </div>
        );
    };

    handleContinueBtnClick = () => {};

    baseCls = 'dd-switch-bump';
}

export default Bump;
