import { post } from '@Green-Dot-Corporation/web-app-utils-api-lib';
import { getUrlConfig } from '../utils/urlHelper';

export default {};

export const getDDConfig = async () => {
    // TODO: to be confirmed with gateway api
    // const { GET_CONFIG } = getUrlConfig();
    // const config = await get(GET_CONFIG);
    const resp = {
        isDDFeatureEnabled: true,
    };

    return resp;
};

export const requestUserToken = async () => {
    const { REQUEST_USER_TOKEN } = getUrlConfig();
    const rawResp = await post(REQUEST_USER_TOKEN);
    const resp = {
        token: rawResp.token,
        email: rawResp.email,
    };

    return resp;
};
