import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import {
    getTranslation,
    reactI18next,
} from '@Green-Dot-Corporation/eureka-lib-i18n-utils';
import { Hyperlink } from '@Green-Dot-Corporation/eureka-ui-typography';
import './styles/legal.scss';

class Legal extends PureComponent {
    static propTypes = {
        contextCls: PropTypes.string,
    };

    render() {
        return (
            <div className={`${this.baseCls}__dd-legal-claimer`}>
                {this.renderFooter()}
            </div>
        );
    }

    renderFooter = () => {
        const { contextCls } = this.props;
        const { Trans } = reactI18next;

        return (
            <div className={cx(this.baseCls, contextCls)}>
                <Trans
                    i18nKey="legal.ddLegalDisclaimer.copy.text"
                    components={{
                        sup: <sup />,
                        learnMoreLink: (
                            <Hyperlink
                                shouldOpenInNewWindow
                                href={getTranslation(
                                    'legal.ddLegalDisclaimer.link.url',
                                )}
                            />
                        ),
                    }}
                />
            </div>
        );
    };

    baseCls = 'dd-switch-legal';
}

export default Legal;
