import { PureComponent } from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';

// Styles
import './styles/dd-switch-container.scss';

class DDSwitchContainer extends PureComponent {
    static propTypes = {
        contextCls: PropTypes.string,
    };

    state = {};

    render() {
        const { contextCls, children } = this.props;

        return <div className={cx(this.baseCls, contextCls)}>{children}</div>;
    }

    baseCls = 'dd-switch-container';
}

export default DDSwitchContainer;
